@mixin centY {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}

@mixin cent {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

@mixin centX {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

@mixin stretch {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

@function pxtopercent($target, $parent: 1200) {
	@return percentage($target / $parent);
}